import { DatePipe } from '@angular/common';
import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  OnInit,
  ViewChild,
  ViewContainerRef,
} from '@angular/core';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { MomentDateAdapter } from '@angular/material-moment-adapter';
import {
  DateAdapter,
  MAT_DATE_FORMATS,
  MAT_DATE_LOCALE,
} from '@angular/material/core';
import { Router } from '@angular/router';
import {
  ToastController,
  ModalController,
  LoadingController,
} from '@ionic/angular';
import { map, startWith } from 'rxjs/operators';
import { DenominationSlideComponent } from 'src/app/components/denomination-slide/denomination-slide.component';
import { TimeSlotsComponent } from 'src/app/components/time-slots/time-slots.component';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { LoadingService } from 'src/app/services/loading.service';
import { ToastService } from 'src/app/services/toast.service';
import { MY_FORMATS } from '../forex-cash-v2/forex-cash-v2.page';
import { BranchComponent } from 'src/app/components/branch/branch.component';
import { TokenV2Page } from '../../token-v2/token-v2.page';
import { Observable } from 'rxjs';
import { TransactionDateComponent } from 'src/app/components/transaction-date/transaction-date.component';
import { RetailService } from 'src/app/services/retail.service';

@Component({
  selector: 'app-cash-withdraw-v2',
  templateUrl: './cash-withdraw-v2.page.html',
  styleUrls: ['./cash-withdraw-v2.page.scss'],
  animations: [
    trigger('openClose', [
      state('true', style({ height: '*' })),
      state('false', style({ height: '0px' })),
      transition('false <=> true', [animate(500)]),
    ]),
  ],
  providers: [
    {
      provide: DateAdapter,
      useClass: MomentDateAdapter,
      deps: [MAT_DATE_LOCALE],
    },
    { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
  ],
})
export class CashWithdrawV2Page implements OnInit {
  @ViewChild('dynamic', { read: ViewContainerRef })
  private chwViewRef: ViewContainerRef;
  @ViewChild('denom', { read: ElementRef }) denomButtonRef: ElementRef;

  public form: FormGroup;
  depositType: string = 'Cash Withdraw';
  homeIconToggle: boolean = false;
  currentBalance: any;
  transAmt: any;
  animationState = 'out';
  selectedCountryCode: string;
  isShow: boolean = true;
  repFG: FormGroup = this.formBuilder.group({
    personName: ['', Validators.required],
    phoneNo: [
      '',
      [Validators.required, Validators.minLength(10), Validators.maxLength(10)],
    ],
    repdocumentId: [''],
    repdocumentNumber: [''],
  });
  frmToggle: boolean;
  ftmToggle: boolean;
  @ViewChild('dynamic', { read: ViewContainerRef })
  private viewRef: ViewContainerRef;
  currencies: any;
  selectedCountryCodeForexTrans: string = '';

  currentUser: any;
  phoneNumber: string;
  customerId: string;
  routerData: any;
  editMode: boolean = false;
  selectedCurrency: any;
  accountInfo: any;
  chargeAmount: any = 5;
  selectedAccount: any;
  filteredAccount: any = [];
  isShowDenomError: boolean = false;
  transAmount: any;
  isShowRepError: boolean = false;
  cashWithdrawResponse: any;
  transactionId: any;
  customerDetails: any;
  currencyControl = new FormControl<string>('');
  filteredOptions: Observable<any[]>;
  flagValue: any;
  initialEx: string = 'inr';
  denomFlag: boolean = true;
  accounts: any;
  branches: any = [];

  constructor(
    private router: Router,
    private formBuilder: FormBuilder,
    private api: ApiService,
    public toastCtrl: ToastController,
    public toastService: ToastService,
    private modalController: ModalController,
    public loading: LoadingService,
    public datepipe: DatePipe,
    private shareDataService: DataService,
    private cdr: ChangeDetectorRef,
    private retailService: RetailService
  ) {}

  ngOnInit() {
    this.currentUser = this.shareDataService.getCurrentUser();
    let accounts: any = JSON.parse(localStorage.getItem('ACCOUNT'));
    console.log('ACCOU', accounts);
    this.accounts = accounts?.accountInfo;
    this.selectedAccount = this.accounts[0]?.accountId;
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));
    let customerDetails: any = JSON.parse(
      localStorage.getItem('customer_details')
    );
    this.customerDetails = customerDetails[0];
    let branches = JSON.parse(localStorage.getItem('BRANCHES'));
    this.branches = branches;

    console.log('ACCOU', this.accounts);

    this.flagValue = 'in';

    if (this.router.getCurrentNavigation() != null) {
      this.routerData = this.router.getCurrentNavigation().extras.state;
      this.editMode = this.routerData?.data ? true : false;
    }

    this.formInit();
    this.upsertAccount();

    if (this.editMode) {
      const data = this.routerData?.data;
      const countryCode = this.currencies.filter(
        (i: any) => i.currencyCode === data?.transactionCurrency
      );
      this.selectedCurrency = countryCode[0];
      this.selectedCountryCode =
        this.selectedCurrency?.countryCode.toLowerCase();
    } else {
      const countryCode = this.currencies.filter(
        (i: any) => i.currencyCode === 'INR'
      );
      this.selectedCurrency = countryCode[0];
    }

    this.shareDataService.getAccountInfo.subscribe((data) => {
      console.log('getting data', data);
      this.accountInfo = data;
    });

    this.form.get('transactionAmount').valueChanges.subscribe((val) => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
      }
    });

    this.filteredOptions = this.currencyControl.valueChanges.pipe(
      startWith(''),
      map((value: any) => {
        const countryCode =
          typeof value === 'string' ? value : value?.countryCode;
        return countryCode
          ? this._filter(countryCode as string)
          : this.currencies.slice();
      })
    );

    let today = new Date().toISOString();
    const convertedDate = this.datepipe.transform(today, 'yyyy-MM-dd');
    this.form.get('transactionDate').patchValue(convertedDate);
  }

  formInit() {
    this.form = this.formBuilder.group({
      // customerId: [''],
      accountNo: ['', Validators.required],
      transactionType: [this.depositType, Validators.required],
      exchangeRate: [''],
      narrative: [''],
      drCr: 'C',
      denomination: [null],
      transactionCurrency: [''],
      transactionBranch: ['', [Validators.required]],
      transactionAmount: ['', [Validators.required]],
      totalChargeAmount: [this.chargeAmount, [Validators.required]],
      totalTransactionAmount: ['', [Validators.required]],
      transactionDate: ['', [Validators.required]],
      transactionTime: ['', [Validators.required]],
    });
  }

  private _filter(currency: string): any[] {
    const filterValue = currency.toLowerCase();
    return this.currencies.filter((option) =>
      option.countryCode.toLowerCase().includes(filterValue)
    );
  }

  onScreenRefresh() {
    this.editMode = this.routerData?.data ? true : false;
    this.currentUser = this.shareDataService.getCurrentUser();
    this.phoneNumber = localStorage.getItem('customerPhonenum');
    this.customerId = sessionStorage.getItem('customer_id');
    this.currencies = JSON.parse(localStorage.getItem('COUNTRIES'));

    if (this.editMode) {
      const data = this.routerData?.data;
      const countryCode = this.currencies.filter(
        (i: any) => i.currencyCode === data?.transactionCurrency
      );
      this.selectedCurrency = countryCode[0];
      this.selectedCountryCode =
        this.selectedCurrency?.countryCode.toLowerCase();
    } else {
      const countryCode = this.currencies.filter(
        (i: any) => i.currencyCode === 'INR'
      );
      this.selectedCurrency = countryCode[0];
    }

    this.formInit();

    this.shareDataService.getAccountInfo.subscribe((data) => {
      this.accountInfo = data;
    });

    this.form.get('transactionAmount').valueChanges.subscribe((val) => {
      if (val) {
        this.denomButtonRef?.nativeElement.classList.add('active-bg');
        this.updateTransAmount();
        this.denomValidationListener();
      }
    });
  }

  onBlurEvent(e: any) {
    this.updateTransAmount();
    this.denomValidationListener();
  }

  getExChangeRate(currency) {
    this.api
      .getExchangeRate(this.accountInfo.accountCurrency)
      .subscribe((res: any) => {
        const code = this.currencies?.filter(
          (x: any) => x.currencyCode == currency
        );
        let filteredCurrency = code[0].currencyCode;
        const rate = res?.rates?.[filteredCurrency];
        this.form.get('exchangeRate').patchValue(rate);
        let toFix = this.form.get('transactionAmount')?.value;
        let val =
          toFix != 'undefined' || toFix != undefined
            ? toFix?.replace(/,/g, '')
            : 0;
        if (!isNaN(val)) {
          let updatedAmount =
            val * this.form.get('exchangeRate')?.value + this.chargeAmount;
          if (!isNaN(updatedAmount)) {
            // let formatedAmount: any = this.shareDataService.formatCurrency(
            //   updatedAmount,
            //   this.form.get('transactionCurrency')?.value
            // );
            this.form.get('totalTransactionAmount').patchValue(updatedAmount);
            this.cdr.markForCheck();
          }
        }
      });
    this.updateTransAmount();
  }

  updateTransAmount() {
    let toFix = this.form.get('transactionAmount')?.value;
    let val =
      toFix != 'undefined' || toFix != undefined ? toFix?.replace(/,/g, '') : 0;
    if (!isNaN(val)) {
      let updatedAmount =
        Number(val) * (this.form.get('exchangeRate')?.value || 1) +
        this.chargeAmount;
      if (!isNaN(updatedAmount)) {
        // let formatedAmount: any = this.shareDataService.formatCurrency(
        //   updatedAmount,
        //   this.form.get('transactionCurrency')?.value
        // );
        this.form.get('totalTransactionAmount').patchValue(updatedAmount);
        this.cdr.markForCheck();
      }
    }
  }

  getHomeBranch() {
    this.homeIconToggle = !this.homeIconToggle;
    if (this.homeIconToggle) {
      console.log(this.filteredAccount?.accountBranch);
      this.form
        .get('transactionBranch')
        ?.patchValue(this.branches[0]?.branchCode);
    } else {
      this.form.get('transactionBranch')?.patchValue('');
    }
  }

  upsertAccount() {
    if (this.editMode) {
      const data = this.routerData?.data;

      this.form.get('accountNo')?.patchValue(data?.accountNumber);
      this.form
        .get('transactionCurrency')
        ?.patchValue(data?.transactionCurrency);
      this.form.get('exchangeRate')?.patchValue(data?.exchangeRate);
      let toInt = parseInt(data?.transactionAmount);
      let toLocale = toInt.toLocaleString('en-IN');
      this.form.get('transactionAmount')?.patchValue(toLocale);
      let formatedAmount: any = this.shareDataService.formatCurrency(
        data?.totalTransactionAmount,
        data?.transactionCurrency
      );
      this.form.get('totalTransactionAmount').patchValue(formatedAmount);
      this.form.get('transactionBranch')?.patchValue(data?.transactionBranch);
      this.form.get('transactionDate')?.patchValue(data?.transactionDate);
      this.form.get('transactionTime')?.patchValue(data?.transactionTime);
      this.form.get('narrative')?.patchValue(data?.narrative);
    } else {
      console.log('accounti info', this.accountInfo);
      if (this.accountInfo?.accountId != null) {
        this.form.get('accountNo').patchValue(this.accountInfo.accountId);
      } else {
        this.form.get('accountNo').patchValue(this.accounts[0].accountId);
      }

      this.selectedCountryCode =
        this.selectedCurrency?.countryCode.toLowerCase();
      this.form
        .get('transactionCurrency')
        ?.patchValue(this.selectedCurrency?.currencyCode);
    }
    this.cdr.markForCheck();
  }

  datePopup() {
    let oldDate = this.form.get('transactionDate')?.value;
    this.modalController
      .create({
        component: TransactionDateComponent,
        componentProps: {
          date: this.form.get('transactionDate').value,
        },
      })
      .then((modalResp) => {
        modalResp.present();
        modalResp.onDidDismiss().then((res) => {
          console.log(typeof res?.data);
          if (typeof res?.data == 'undefined') {
            this.form.get('transactionDate')?.patchValue(oldDate);
          } else {
            this.form.get('transactionDate')?.patchValue(res.data);
          }
        });
      });
  }

  openPopup() {
    this.modalController
      .create({
        component: TimeSlotsComponent,
        componentProps: {
          date: this.form.get('transactionDate').value,
        },
      })
      .then((modalResp) => {
        modalResp.present();
        modalResp.onDidDismiss().then((res) => {
          if (
            res.data != null ||
            res.data != undefined ||
            res.data != 'undefined'
          ) {
            this.form.get('transactionTime').patchValue(res.data?.form);
          }
        });
      });
  }

  keyPressMob(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  selectCurrencyCode(e: any) {
    this.flagValue = e.toString().slice(0, 2);
    if (e == 'undefined' || e == undefined || e == null || e == 'object') {
      return;
    } else {
      if (e.length > 2) {
        const filteredCurrency = this.currencies?.filter(
          (i: any) => i.currencyCode === e
        );
        this.selectedCountryCode =
          filteredCurrency[0].countryCode.toLowerCase();
        this.getExChangeRate(filteredCurrency[0].currencyCode);
        this.cdr.markForCheck();
      }
    }
  }

  displayFn(val) {
    return val ? val : 'in';
  }

  onAccountSelect(e: any) {
    console.log('e', e);
    let filteredAccount: any;

    if (this.selectedAccount) {
      filteredAccount = this.accounts?.filter(
        (account) => account.accountId === this.selectedAccount
      );
    } else {
      filteredAccount = this.accounts[0];
    }

    this.filteredAccount = filteredAccount[0];
    const countryCode = this.currencies?.filter(
      (i: any) => i.currencyCode === this.filteredAccount?.accountCurrency
    );

    console.log('e', e);

    this.selectedCurrency = countryCode[0];
    this.selectedCountryCode =
      this.selectedCurrency?.countryCode?.toLowerCase();
    this.form
      .get('transactionCurrency')
      ?.patchValue(this.filteredAccount?.accountCurrency);
  }

  getChargeDetails() {
    this.api.getChargeDetails(this.accountInfo.accountId).subscribe(
      (resp) => {
        this.chargeAmount = resp?.[0]?.amount;
        this.form.get('totalChargeAmount').patchValue(resp?.[0]?.amount);
      },
      (err) => console.log('Error: ', err)
    );
  }

  enableDisableAction(type: any) {
    switch (type) {
      case 'TXAM':
        this.toggleFrm();
        break;
      default:
        console.log('No such type exists!');
        break;
    }
  }

  goBack() {
    this.router.navigate(['dashboard']);
  }

  balanceCheck(event: any) {
    this.transAmt = event.target.value;
    this.transAmt = this.transAmt.toString().replace(/,/g, '');
    this.currentBalance = this.filteredAccount.amount;
    this.updateTransAmount();
    if (parseFloat(this.currentBalance) < parseFloat(this.transAmt)) {
      this.showToast();
    } else {
      return;
    }
  }

  formatAccountBalance(balance: any) {
    if (balance > 0) {
      let toInt = parseInt(balance);
      let toLocale = toInt.toLocaleString('en-IN');
      return toLocale;
    } else {
      let toInt = parseInt('0');
      let toLocale = toInt.toLocaleString('en-IN');
      return toLocale;
    }
  }

  keyPressNumbers(event) {
    var charCode = event.which ? event.which : event.keyCode;
    // Only Numbers 0-9
    if (charCode < 48 || charCode > 57) {
      event.preventDefault();
      return false;
    } else {
      return true;
    }
  }

  async showToast() {
    const toast = await this.toastCtrl.create({
      message: 'Transaction Amount should not exceed than Account Balance',
      duration: 2000,
      position: 'bottom',
    });
    toast.present();
  }

  toggleFrm() {
    this.frmToggle = !this.frmToggle;
    this.animationState = this.animationState === 'out' ? 'in' : 'out';
    this.createComponent();
  }

  createComponent() {
    this.chwViewRef.clear();
    let childComponentRef = this.chwViewRef.createComponent(
      DenominationSlideComponent
    );
    let childComponent = childComponentRef.instance;
    childComponent.unique_key = 'chwViewRef';
    childComponent.chwViewRef = this;
    childComponent.emitTotalValue.subscribe((value) => {
      if (value && value != this.form.get('transactionAmount')?.value) {
        this.denomFlag = false;
      } else {
        this.denomFlag = true;
      }
    });
  }

  removeDynamicComponent(): void {
    this.chwViewRef.clear();
  }

  remove(view: any) {
    let data = JSON.parse(localStorage.getItem(view)) || null;
    if (view == 'chwViewRef') {
      this.chwViewRef.clear();
      if (data != null && data.total != 0) {
        this.denomAmountValidation(data.total);
        console.log(this.form.value);
      }
    }
  }

  denomAmountValidation(amount: any) {
    if (
      Number(this.form.get('transactionAmount')?.value.split(',').join('')) !=
      Number(amount)
    ) {
      this.isShowDenomError = true;
      this.toastService.showToast(
        'Denomination total should match with the transaction amount'
      );
    } else {
      this.isShowDenomError = false;
    }
    this.cdr.markForCheck();
  }

  denomValidationListener() {
    let data = JSON.parse(localStorage.getItem('chwViewRef')) || null;
    if (data?.title == 'chwViewRef') {
      if (data != null && data.total != 0) {
        this.denomAmountValidation(data.total);
      }
    }
  }

  submit(form: any) {
    var date = new Date(form.transactionDate);
    let latest_date = this.datepipe.transform(date, 'yyyy-MM-dd');
    form.transactionDate = latest_date;
    form.customerId = this.customerId;
    form.representativeInfo = [];
    form.transactionAmount = Number(this.form.get('transactionAmount')?.value);

    if (this.editMode) {
      form.appointmentId = this.routerData.data.appointmentId;
      form.operation = 'update';
    }

    // Total transaction convertion format
    // let ttam = this.form.value['totalTransactionAmount'].substring(1);
    // console.log(ttam);
    // form.totalTransactionAmount = ttam.replace(/,/g, '');

    if (this.editMode) {
      form.transactionAmount = form.transactionAmount.split(',').join('');
      this.api.appointmentBooking(form).subscribe((resp) => {
        localStorage.setItem('TransactionTime', resp.transactionTime);
        this.cashWithdrawResponse = resp;
        this.transactionId = this.cashWithdrawResponse.transactionId;
        localStorage.setItem('AppointmentDetails', JSON.stringify(resp));
        console.log('transactionId::', this.transactionId);
        if (
          this.cashWithdrawResponse === 200 ||
          this.cashWithdrawResponse !== null
        ) {
          this.shareDataService.shareTransactionId(this.transactionId);
          this.onClick(this.cashWithdrawResponse);
          this.form.reset();
        }
      });
    } else {
      form.transactionAmount = form.transactionAmount
        .toString()
        .replace(/,/g, '');

      this.retailService.createAppointment(form).subscribe((resp: any) => {
        if (resp?.statusCode == 200) {
          this.cashWithdrawResponse = resp;
          this.onClick(this.cashWithdrawResponse);
          this.form.reset();
        }
      });
    }
  }

  async onClick(event) {
    let modal = await this.modalController.create({
      component: TokenV2Page,
      componentProps: {
        value: event?.data,
        screen: 'onScreen',
      },
    });

    modal.onDidDismiss().then((res) => {
      this.routerData = res;
      console.log('Getting Back Data', this.routerData);
      this.onScreenRefresh();
    });

    return await modal.present();
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: BranchComponent,
      id: 'branchModal',
      componentProps: {},
    });

    modal.onDidDismiss().then((modelData) => {
      if (
        modelData.data != null ||
        modelData.data != undefined ||
        modelData.data != 'undefined'
      ) {
        console.log(modelData.data['data'].branchName, 'branch 4');
        this.form
          .get('transactionBranch')
          .patchValue(modelData.data['data'].branchCode);
        if (
          this.form.get('transactionBranch').value ==
          this.filteredAccount?.accountBranch
        ) {
          this.homeIconToggle = true;
        } else {
          this.homeIconToggle = false;
        }
      }
    });

    return await modal.present();
  }

  goToHomepage() {
    this.router.navigate(['/dashboard']);
  }

  ngOnDestroy(): void {
    localStorage.removeItem('chwViewRef');
  }

  onInput(value: any = '') {
    let commasRemoved = value && value.replace(/,/g, '');
    let toInt: number;
    let toLocale: string;
    if (commasRemoved && commasRemoved.split('.').length > 1) {
      let decimal = isNaN(parseInt(commasRemoved.split('.')[1]))
        ? ''
        : parseInt(commasRemoved.split('.')[1]);
      toInt = parseInt(commasRemoved);
      toLocale = toInt.toLocaleString('en-IN') + '.' + decimal;
    } else {
      toInt = parseInt(commasRemoved);
      toLocale = toInt.toLocaleString('en-IN');
    }
    if (toLocale === 'NaN') {
      return '';
    } else {
      return toLocale;
    }
  }

  onKeyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
    console.log(event.target.value);
    if (!event.target.value) {
      // this.filteredOptions = []
    }
  }

  onKey(event) {
    console.log(event);

    this.filteredOptions = this.currencyControl.valueChanges.pipe(
      startWith(event),
      map((value: any) => {
        const countryCode =
          typeof value === 'string' ? value : value?.countryCode;
        return countryCode
          ? this._filter(countryCode as string)
          : this.currencies.slice();
      })
    );
  }
}
